@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background: #000;
}

.linearBorder {
  /* border: 1px solid transparent; */
  border-style: solid;
  border-color: transparent;
  /* border-radius: 60px; */
  background-clip: padding-box, border-box;
  background-origin: padding-box, border-box;
  background-image: linear-gradient(to right, #000, #000), linear-gradient(90deg, #60a5fa, #22c55e);
}

.borderColor {
  border-image: linear-gradient(90deg, #60a5fa, #22c55e) 6 6;
  border-style: solid;
  border-radius: 10px;
}

.minHeight {
  min-height: calc(100vh - 64px);
}

.ant-drawer-content{
  background:#000!important;
  color:#fff;
}